    methods: {
        close() {
            anime({
                targets: '.pic > img',
                scale: 0,
                easing: 'easeInOutQuad'
            });
        },
        open() {
            anime({
                targets: '.pic > img',
                scale: 1,
                easing: 'easeInOutQuad'
            });
        }
    },
}
