<template>
    <div class="home">
        <headerbar></headerbar>
        <Moveable v-bind="moveable" @drag="handleDrag" @click="changeindex">
            <!-- <span>Vue Moveable</span> -->
            <introblock :indexprops="currentindex"></introblock>
        </Moveable>
        <Moveable v-bind="moveable" @drag="handleDrag">
            <!-- <span>Vue Moveable</span> -->
            <picblock></picblock>
        </Moveable>
        <Moveable v-bind="moveable" @drag="handleDrag">
            <!-- <span>Vue Moveable</span> -->
            <motionticket></motionticket>
        </Moveable>
        <Moveable v-bind="moveable" @drag="handleDrag">
            <!-- <span>Vue Moveable</span> -->
            <projectblock topprops="20" leftprops="65" mobileleftprops="10" mobiletopprops="98" projectpic="https://i.imgur.com/fZdVwrl.jpg"></projectblock>
        </Moveable>
        <Moveable v-bind="moveable" @drag="handleDrag" class="move">
            <!-- <span>Vue Moveable</span> -->
            <projectblock projectpic="https://i.imgur.com/WOTiN6z.jpg" topprops="90" leftprops="15" mobileleftprops="20" mobiletopprops="139"></projectblock>
        </Moveable>
    
    
    
    </div>
</template>

<script>
import headerbar from '../components/menu'
import introblock from '../components/introblock'
import picblock from '../components/picblock'
import Moveable from 'vue-moveable';
import motionticket from '../components/motionticket'
import projectblock from '../components/projectblock'
export default {
    name: 'Home',
   
    data() {
        return {
            currentindex:0,
            moveable: {
                
                draggable: true,
                throttleDrag: 0,
                throttleDragRotate: 0,
                // resizable: false,
                // throttleResize: 1,
                // keepRatio: false,
                // scalable: false,
                // throttleScale: 0,
                // rotatable: false,
                // throttleRotate: 0,
                // pinchable: true, 
                // origin: false,
            }
        }
    },
    mounted() {
        if(document.body.clientWidth<=480){
            this.moveable.draggable = false;
            console.log(document.body.clientWidth)
        }else{
            this.moveable.draggable = true
            console.log(document.body.clientWidth)
        }
    
    },
    methods: {
        changeindex(){
           
        },
        handleDrag({ target, transform }) {
            target.style.transform = transform;
             this.currentindex = 3;  
             
            console.log(this.currentindex);
        },
        handleResize({
            target,
            width,
            height,
            delta,
        }) {
            console.log('onResize', width, height);
            delta[0] && (target.style.width = `${width}px`);
            delta[1] && (target.style.height = `${height}px`);
        },
        handleScale({ target, transform, scale }) {
            console.log('onScale scale', scale);
            target.style.transform = transform;
        },
        handleRotate({ target, dist, transform }) {
            console.log('onRotate', dist);
            target.style.transform = transform;
        },
        handleWarp({ target, transform }) {
            console.log('onWarp', transform);
            target.style.transform = transform;
        },
        handlePinch({ target }) {
            console.log('onPinch', target);
        },
    },

    components: {
        introblock,
        Moveable,
        picblock,
        motionticket,
        headerbar,
        projectblock
    }
}
</script>

